.filterBySchool {
    width: 100%;
    max-width: 216px;
}
.filterName {
    color: #818182;
    line-height: 28px; /* 175% */
    margin-bottom: 3px;
}
.select {
    color: #ABB5BE;
    line-height: 21px; /* 131.25% */
    padding: 8px 15px 9px;
    max-width: 216px;
    width: 100%;
    border-radius: 4px;
    border: 1px solid #CED4DA;
    background-color: #FFF;
    height: 38px;
    cursor: pointer;
}
